import { ComponentChild, Fragment } from 'preact';
import { useCallback } from 'preact/hooks';
import { Transition, TransitionGroup } from '../UNSAFE_TransitionGroup';
import { Item } from '../utils/UNSAFE_dataProvider';
import { useMessagesContext } from './MessagesContext';

/**
 * A type that defines animation configuration
 */
type MessageAnimation = {
  effect?: string;
  duration?: string | number;
  direction?: string;
};

/**
 * Transition states for animations
 */
type TransitionState = 'enter' | 'exit';

/**
 * Metadata passed on to the Transition component
 */
type TransitionMetadata<K> = {
  index: number;
  key: K;
};

/**
 * Type of the render context passed on to render individual message
 */
type MessageRenderContext<Key, Data> = {
  /**
   * The index of the current message
   */
  index: number;

  /**
   * The message item with all the necessary data in it
   */
  item: Item<Key, Data>;
};

// Type of startAnimation property
type StartAnimType = (
  element: Element,
  action: string,
  effects: string | object | (string | object)[],
  component?: object
) => Promise<unknown>;

/**
 * Props for the Message Component
 */
type Props<Key, Data> = {
  /**
   * An object of various animation effects for the transitions.
   */
  animations?: Record<string, MessageAnimation[]>;

  /**
   * A render function that renders individual child.
   */
  children?: (context: MessageRenderContext<Key, Data>) => ComponentChild;

  /**
   * Data for the messages. This data is used for rendering each message.
   */
  data: Item<Key, Data>[];

  /**
   * Method for starting an animation.
   */
  startAnimation?: StartAnimType;

  /**
   * A callback function that gets called before a message is removed
   */
  onMessageWillRemove?: (key: Key, index: number, node?: Element) => void;
};

/**
 * The component that renders individual messages for the provided data.
 */
function MessagesManager<K extends string | number = string | number, D = any>(props: Props<K, D>) {
  const { children, data } = props;
  const { handleEntering, handleExiting } = useMessagesManager(props);

  return (
    <TransitionGroup elementType={Fragment}>
      {data.map((item, index) => (
        <Transition
          key={item.key}
          metadata={{ index, key: item.key }}
          onEntering={handleEntering}
          onExiting={handleExiting}>
          {children?.({ index, item })}
        </Transition>
      ))}
    </TransitionGroup>
  );
}

/**
 * A custom hook for creating the listeners for the MessagesManager
 *
 * @param param0 The props for the messages
 * @returns The transition listeners
 */
function useMessagesManager<K, D>({
  animations,
  startAnimation = () => Promise.resolve(false),
  onMessageWillRemove
}: Props<K, D>) {
  const { addBusyState } = useMessagesContext();

  /**
   * Adds busy state if available in the context
   *
   * @param description The description of the busyState
   * @returns The busyState resolver
   */
  const _addBusyState = useCallback(
    (description: string) => {
      return addBusyState?.(description) ?? (() => {});
    },
    [addBusyState]
  );

  /**
   * Performs animation.
   *
   * @param type The type of the animation
   * @param base The root DOM element
   */
  const performAnimation = useCallback(
    async (type: TransitionState, base?: Element) => {
      if (!base) {
        return;
      }

      const animation = animations?.[type];
      if (animation) {
        const busyStateResolver = _addBusyState(`performing message animation - ${type}`);
        // If an animation is provided for the current transition, perform the animation
        await startAnimation(base, type, animation);
        busyStateResolver();
      }
    },
    [animations, startAnimation, _addBusyState]
  );

  /**
   * Handles when a message is successfully entered.
   *
   * @param node The corresponding message element
   * @param callback A callback function to be called after the animation is complete
   */
  const handleEntering = useCallback(
    async (node?: Element, callback?: () => void) => {
      await performAnimation('enter', node);
      callback?.();
    },
    [performAnimation]
  );

  /**
   * Handles when a message has started to exit.
   *
   * @param node The corresponding message element
   * @param callback A callback function to be called after the animation is complete
   */
  const handleExiting = useCallback(
    async (node?: Element, callback?: () => void, metadata?: TransitionMetadata<K>) => {
      await performAnimation('exit', node);
      metadata && onMessageWillRemove?.(metadata.key, metadata.index, node);
      callback?.();
    },
    [performAnimation, onMessageWillRemove]
  );

  return { handleEntering, handleExiting };
}

export { MessageAnimation, MessagesManager };
